$borderColor: 4px solid rgba(253, 203, 168, 1);
$signColor: rgba(171, 118, 146, 1);
$signFont: "PT Sans", sans-serif;

#header {
  text-align: center;
  margin-left: 40%;
  margin-right: 40%;
  margin-top: 10%;
  img {
    width: 85px;
  }
}

.password-login {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
}

#login {
margin-bottom: 20px;
}

#login, #password {
  border: $borderColor;
  padding: 3px 3px 3px 3px;
  width: 300px;
}

.submit {
  margin-top: 20px;
  border: $borderColor;
}

#sign-in {
  border: 0;
  background: none;
  cursor: pointer;
  font-family: $signFont;
  padding-bottom: 2px;
  color: $signColor;
}
